import React, { useState } from 'react';
import Button from './button';
import { useEffect } from 'react';

const Board = () => {
  const [width, setWidth] = useState(3);
  const [player, setPlayer] = useState('X');
  const [squares, setSquares] = useState([]);
  const [widths, setWidths] = useState([3, 4, 6, 10, 20]);
  const [winner, setWinner] = useState('');

  useEffect(() => {
    const fields = Array(width * width).fill(null);
    setSquares(fields);
    setWinner('');
  }, [width]);

  const handleClick = (i) => {
    if (squares[i] === null) {
      squares[i] = player;
      setSquares(squares);
      var turn = player;
      player === 'X' ? (turn = 'O') : (turn = 'X');
      setPlayer(turn);
      calculateWinner();
    }
  };

  const calculateWinner = () => {
    const makeLines = () => {
      for (i = 1; i <= width; i++) {
        chunk = horizontals.splice(0, width);
        lines.push(chunk);
      }
    };
    const makeDiagonals = () => {
      for (i = 1; i <= width; i++) {
        chunk = horizontals.splice(0, i);
        lines.push(chunk);
      }
    };
    const revertedDiagonals = () => {
      for (i = width; i > 0; i--) {
        chunk = horizontals.splice(0, i);
        lines.push(chunk);
      }
    };

    const lines = [];
    var chunk;
    var i;
    var j;
    var squared = width * width + 1;
    var horizontals = [];

    //Horizontals
    for (i = 1; i < squared; i++) {
      horizontals.push(i);
    }
    makeLines();

    //Verticals
    for (i = 1; i <= width; i++) {
      // 1,2,3,4
      for (j = i; j < squared; j += width) {
        //1,5,9,13
        horizontals.push(j);
      }
    }
    makeLines();

    //Diagonals top right
    for (i = 1; i <= width; i++) {
      //1,2,3,4
      for (j = i; j < i * width; j += width - 1) {
        //1,4,7,10  // 4
        //2,5,8,11  // 8
        //3,6,9,12  // 12
        //4,7,10,13 // 16
        horizontals.push(j);
      }
    }
    makeDiagonals();

    for (i = width; i < squared; i += width) {
      //4,8,12,16
      for (j = i; j < squared - 1; j += width - 1) {
        //4,7,10,13 // 16
        //8,11,14 // 16
        //12,15
        //
        horizontals.push(j);
      }
    }
    revertedDiagonals();

    for (i = width; i < squared; i += width) {
      // 4, 8, 12, 16
      for (j = i; j > 0; j -= width + 1) {
        //4  // 4
        //8,3 // 8
        //12,7,2 // 12
        //16,11,6,1 // 16
        horizontals.push(j);
      }
    }
    makeDiagonals();

    for (i = 1; i < squared; i += width) {
      //1,5,9,13
      for (j = i; j < squared; j += width + 1) {
        horizontals.push(j);
      }
    }
    revertedDiagonals();

    var printing = '';
    lines.forEach((line) => {
      line.forEach((l) => (printing = printing + squares[l - 1]));
      printing = printing + '-';
    });
    console.log(lines);

    if (printing.includes('XXX')) {
      setWinner('X wins!');
    }
    if (printing.includes('OOO')) {
      setWinner('O wins!');
    }
  };

  return (
    <div>
      <div className='button-large' onClick={() => setWidth(3)}>
        New Game
      </div>
      {widths.map((width) => (
        <p key={width} onClick={() => setWidth(width)} className='button'>
          play {width}x{width}
        </p>
      ))}
      <div className='status'>Next player {player}</div>
      <div className='status'>{winner}</div>
      <div className={`board-container`} style={{ width: 60 * width }}>
        {squares.map((square, index) => (
          <Button
            value={squares[index]}
            key={index}
            onClick={() => handleClick(index)}
          />
        ))}
      </div>
    </div>
  );
};

export default Board;
